//貓狗首頁按鈕

.dc-button {
  background: rgba(255, 255, 255, 0.6);
  width: 240px;
}

.dc-button:hover {
  background: rgba(255, 255, 255, 0.9);
  outline: 5px solid;

}

//貓狗標題偽元素
.tittle-border:before {
  content: '';
  position: absolute;
  bottom: -30px;
  right: 0%;
  height: 1px;
  width: 65%;
  background-color: $secondary;
}

.tittle-border-dc:before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0%;
  height: 1px;
  width: 65%;
  background-color: $secondary;
}

//貓狗banner
.banner-tittle {
  height: 25vh;
}

.fs-100 {
  font-size: 100px;
}


.nav-button{
  position: relative;
  
  &::before{
    content: "";
    position: absolute;
    left: 0;
    bottom:0;
    height: 6px;
    width: 100%;
    margin-left: auto;
    background-color: rgb(255, 255, 255);
    transform: scale(0);
  }
  &:hover:before{
    transition-duration: 0.3s;
    transform: scale(1);
  }
  &:hover{
    color: white !important;
  }
}


  





